.post {
	color: var(--blue-main);
	padding-bottom: 1rem;
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
}

.post div:first-child {
	flex-basis: 3rem;
}

.post img {
	width: 2.5rem;
}

.iconWrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.likeWrapper {
	display: flex;
	align-items: center;
	cursor: pointer;
}

.likesCount {
	font-size: 0.8rem;
}
